.main-nav__link {
  @include text(17px, 24px, $bold);
  color: $base-color-dark;
  text-decoration: none;
}

.main-nav__description {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.main-nav__toggle {
  @media (min-width: $tablet-width) {
    display: none;
  }
}
